export default {
  props: {
    withoutid: {
      type: String,
      default: null,
    },
  },
  data() {
    return {
      allCoaches: [],
      loading: true,
      error: {
        status: false,
        text: '',
      },
    }
  },
  async mounted() {
    try {
      await this.$fire.functions
        .httpsCallable('user-getCoaches')()
        .then((d) => {
          d.data.forEach((a) => {
            const b = JSON.parse(a)
            if (this.withoutid) {
              if (this.withoutid !== b.uid) this.allCoaches.push(b)
            } else {
              this.allCoaches.push(b)
            }
          })
          this.loading = false
        })
    } catch (error) {
      error.status = true
      error.text = error
    }
  },
  fetchOnServer: false,
}
